.daily-weather {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: 414px;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  /*height: 50px;*/
  margin: 3px 0;
  padding: 0 1rem;
  color: white;
}

.daily-forecast-img {
  width: 30px;
  margin: 5px;
}

.daily-weather h3 {
  font-size: 1rem;
  text-align: end;
  margin: 0;
}

.daily-weather h2 {
  font-size: 1rem;
}


