.bottom-bar {
/*  position: fixed;
  bottom: 0;
  left: 0;*/
  align-self: end;
  width: 414px;
  height: 50px;
  background: #fff;
  display: grid;
  grid-template-columns: 33.33% 33.33% 33.33%;
}

.bottom-bar p {
  margin: 0;
}

.ten-day-button, 
.current-button {
  border-right: solid 1px rgba(0, 0 ,0 .1);
}

.button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  cursor: pointer;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
}

.ten-day-button-inactive,
.current-button-inactive,
.seven-hour-button-inactive {
  background: #1B353A;
  color: #fff;
}

.button h4 {
  text-align: center;
  margin: 0;
}


