*, body {
  margin: 0;
  padding: 0;
}

.App {
  background: url('./images/nmbus-back.png');
  background-size: cover;
  max-width: 414px;
  height: 100vh;
  overflow: hidden;
}

.App main {
  position: relative;
  width: 414px;
  height: 100%;
  background: rgba(0, 0, 0, .4);
  overflow: hidden;
  display: grid;
}

.loading-text {
  font-size: 1.5rem;
  color: #fff;
  text-align: center;
  margin-top: 0;
}


